.tag-icon{
    margin-right: 5px;
}

.Select-value{
    position: inherit;
    margin: 5px;
    padding: 5px 10px 5px 5px;
}

.fa {

    &.flag-aborginal{
        width: 20px;
        height: 10px;
        background: url('/country-flags/aborginal.png') no-repeat center center;
        background-size: contain;
    }

    &.flag-eu{
        width: 20px;
        height: 10px;
        background: url('/country-flags/eu.png') no-repeat center center;
        background-size: contain;
    }

    &.flag-us{
        width: 20px;
        height: 10px;
        background: url('/country-flags/us.png') no-repeat center center;
        background-size: contain;
    }

    &.flag-uk{
        width: 20px;
        height: 10px;
        background: url('/country-flags/uk.png') no-repeat center center;
        background-size: contain;
    }

    &.flag-aus{
        background: url('/country-flags/aus.png') no-repeat center center;
        background-size: contain;
    }
}



